import { CheckCircle, XCircle, Pencil, Trash2 } from 'lucide-react'

interface Stop {
  id: string
  name: string
  code: string
  address: string
  latitude: number
  longitude: number
  pmr: boolean
  status: 'active' | 'inactive'
}

interface StopsTableProps {
  stops: Stop[]
  onToggleStatus: (id: string) => void
}

export default function StopsTable({ stops, onToggleStatus }: StopsTableProps) {
  return (
    <div className="overflow-x-auto bg-white rounded-lg shadow border border-gray-200">
      <table className="w-full">
        <thead>
          <tr className="border-b border-gray-200 bg-gray-50">
            <th className="px-4 py-3 text-left text-sm font-semibold text-gray-900">Nom</th>
            <th className="px-4 py-3 text-left text-sm font-semibold text-gray-900">Code</th>
            <th className="px-4 py-3 text-left text-sm font-semibold text-gray-900">Adresse</th>
            <th className="px-4 py-3 text-left text-sm font-semibold text-gray-900">Coordonn√©es</th>
            <th className="px-4 py-3 text-left text-sm font-semibold text-gray-900">PMR</th>
            <th className="px-4 py-3 text-left text-sm font-semibold text-gray-900">Statut</th>
            <th className="px-4 py-3 text-left text-sm font-semibold text-gray-900">Actions</th>
          </tr>
        </thead>
        <tbody>
          {stops.map((stop) => (
            <tr key={stop.id} className="border-b border-gray-200 hover:bg-gray-50 transition-colors">
              <td className="px-4 py-3 text-sm font-semibold text-gray-900">{stop.name}</td>
              <td className="px-4 py-3 text-sm text-gray-600 font-mono">{stop.code}</td>
              <td className="px-4 py-3 text-sm text-gray-600">{stop.address}</td>
              <td className="px-4 py-3 text-sm text-gray-600">
                {stop.latitude.toFixed(2)}, {stop.longitude.toFixed(2)}
              </td>
              <td className="px-4 py-3 text-sm">
                {stop.pmr ? (
                  <span className="inline-flex items-center gap-1 px-2 py-1 bg-green-100 text-green-800 rounded text-xs font-semibold">
                    <CheckCircle size={14} /> Oui
                  </span>
                ) : (
                  <span className="inline-flex items-center gap-1 px-2 py-1 bg-red-100 text-red-800 rounded text-xs font-semibold">
                    <XCircle size={14} /> Non
                  </span>
                )}
              </td>
              <td className="px-4 py-3 text-sm">
                <span className={`inline-block px-2 py-1 rounded text-xs font-semibold ${
                  stop.status === 'active'
                    ? 'bg-green-100 text-green-800'
                    : 'bg-red-100 text-red-800'
                }`}>
                  {stop.status === 'active' ? 'Actif' : 'Inactif'}
                </span>
              </td>
              <td className="px-4 py-3 text-sm">
                <div className="flex gap-2">
                  <button
                    onClick={() => onToggleStatus(stop.id)}
                    className={`py-1 px-2 rounded text-xs font-semibold transition-colors ${
                      stop.status === 'active'
                        ? 'bg-red-500 hover:bg-red-600 text-white'
                        : 'bg-green-500 hover:bg-green-600 text-white'
                    }`}
                  >
                    {stop.status === 'active' ? 'D√©sactiver' : 'Activer'}
                  </button>
                  <button className="py-1 px-2 bg-blue-500 hover:bg-blue-600 text-white rounded text-xs font-semibold transition-colors flex items-center gap-1">
                    <Pencil size={14} />
                    Modifier
                  </button>
                  <button className="py-1 px-2 bg-gray-300 hover:bg-gray-400 text-gray-800 rounded text-xs font-semibold transition-colors">
                    <Trash2 size={14} />
                  </button>
                </div>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  )
}
